/* -- Atiestatām stilu -- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* -- Vispārīgie body stili -- */
body {
  font-family: sans-serif;
  background: #f2f2f2;
  color: #333;
}

/* -- Universāls konteineris saturam -- */
.container {
  max-width: 600px;
  margin: 2rem auto;
  background: #fff;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

/* -- Virsrakstu formatējums -- */
h1, h2, h3 {
  text-align: center;
  margin-bottom: 1rem;
}

/* -- Teksta ievades lauks -- */
input[type="text"] {
  display: block;
  margin: 0.5rem auto;
  padding: 0.6rem;
  width: 80%;
  font-size: 1rem;
}

/* -- Pogu stils -- */
button {
  cursor: pointer;
  display: block;
  margin: 1rem auto;
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  border: none;
  background: #007bff;
  color: #fff;
  border-radius: 5px;
}

button:hover {
  background: #0056b3;
}
